    padding: 35px;
    .g-page__header {
        margin-bottom: 10px;
    }
    .g-page__footer {
        padding-top: 20px;
        .g-page__footerSubmit {
            padding-top: 40px;
            text-align: center;
            .u-btn-genarate {
                padding: 0 35px;
                font-size: 16px;
                line-height: 2;
            }
        }
        .g-page__generate {
            margin-top: 40px;
        }
    }
}

.m-header {
    display: flex;
    flex-direction: row;
    /*user-select: none;*/
    .m-header__logo {
        clear: both;
        line-height: 30px;
        color: #444;
        .m-header__logoSide{
            float: left;
            padding-top: 10px;
        }
        .m-header__logoMain{
            overflow: hidden;
        }
        .txt {
            font-size: 26px;
            line-height: 55px;
            vertical-align: middle;
        }
        .version {
            font-size: 14px;
            vertical-align: middle;
        }
        .u-icon-logo-onefly {
            font-size: 30px;
            line-height: 1;
            color: #555;
            vertical-align: middle;
            margin-right: 14px;
        }
    }
    .m-header__show {
        flex: 1;
        text-align: right;
        font-size: 14px;
        line-height: 30px;
        color: #666;
        .m-header__zoom {
            display: inline-block;
            line-height: 30px;
            text-align: left;
            vertical-align: middle;
            .u-icon-zoomin, .u-icon-zoomout {
                font-size: 20px;
                line-height: 30px;
            }
        }
        .m-header__rotate {
            position: relative;
            display: inline-block;
            line-height: 50px;
            text-align: left;
            margin-left: 30px;
            padding-right: 30px;
            vertical-align: middle;
            .u-icon-document, .u-icon-document-h {
                position: absolute;
                right: 10px;
                top: 50%;
                transform: translateY(-50%) rotate(0deg);
                font-size: 24px;
                line-height: 30px;
                &.z-horizontal {
                    transform: translateY(-50%) rotate(90deg);
                }
            }
        }
        .number {
            margin-left: 3px;
            margin-right: 3px;
        }
    }
}

.m-canvas {
    box-sizing: border-box;
    /*overflow: hidden;*/
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 30px;
    outline: 1px dashed #ccc;
    background-color: #fff;
    min-height: 10px;
    &:hover {
        .u-corner-page {
            visibility: visible;
            opacity: 1;
            transition: 0.5s;
        }
    }
    .m-canvas__title {
        font-size: 30px;
        line-height: 1.5;
        text-align: left;
        padding-bottom: 30px;
        .ttl {
            font-size: 30px;
            line-height: 1.5;
            height: 45px;
            outline: 1px dashed #ccc;
        }
    }
    .m-canvas__area {
        position: relative;
        display: flex;
        flex-direction: column;
        flex: 1;
        min-height: 30px;
        &.m-canvas__material {
            .u-corner{
                transform:translateY(-100%);
            }
            &:hover {
                .u-corner-material {
                    visibility: visible;
                    opacity: 1;
                    transition: 0.5s;
                }
            }
        }
        .u-createTxt{
            position: absolute;
            right: 0;
            top: 0;
            transform: translateY(-100%);
            input{
                width: 120px;
                margin-bottom: 3px;
                border: 0;
                font-size: 12px;
                line-height: 1.5;
                color: #666;
            }
        }
    }
    .u-corner {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        left: 0;
        top: 0;
        padding: 0 10px;
        background-color: rgba(200, 200, 100, 0.2);
        font-size: 12px;
        line-height: 1.6;
        color: #666;
    }
}

.m-imageList {
    /*overflow: hidden;*/
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    flex: 1;
    outline: 1px dashed #ccc;
    li {
        overflow: hidden;
        box-sizing: border-box;
        position: relative;
        width: 33.33%;
        flex: 0 0 33.33%;
        margin: 0;
        &:hover{
            .u-icon-closeb{
                visibility: visible;
                opacity: 1;
            }
        }
        .img {
            max-width: 100%;
            max-height: 100%;
        }
        .txt {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            transform: translateY(100%);
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            text-align: center;
            font-size: 14px;
            line-height: 2;
            color: #333;
            text-shadow: 1px 1px 0 #fff;
        }
        .u-icon-closeb {
            visibility: hidden;
            opacity: 0;
            position: absolute;
            left: 0;
            top: 0;
            transform: translate(0%, 0%);
            width: 20px;
            height: 20px;
            background-color: rgba(200, 200, 100, 0.5);
            font-size: 14px;
            color: #333;
            line-height: 20px;
            text-align: center;
            border-radius: 50%;
            cursor: pointer;
        }
    }
}

.m-titleList{
    display: table;
    table-layout: fixed;
    width: 100%;
    font-size: 0;
    line-height: 0;
    li{
        box-sizing: border-box;
        display: table-cell;
        /*padding: 5px;*/
        border: 1px solid #ccc;
        border-right-width: 0;
        white-space: nowrap;
        .item{
            overflow: hidden;
            box-sizing: border-box;
            display: inline-block;
            font-size: 14px;
            line-height: 1.5;
            color: #333;
            text-align: center;
            text-overflow: ellipsis;
            white-space: nowrap;
            border-right: 1px solid #ddd;
        }
    }
}

.m-table{
    border-collapse: collapse;
    box-sizing: border-box;
    width: 100%;
    thead{
        tr{}
        th{
            padding: 0 10px;
            font-size: 16px;
            line-height: 3.5;
            color: #333;
            text-align: left;
            font-weight: 700;
            border: 1px solid #ccc;
            background-color: #eee;
        }
    }
    tbody{
        tr{}
        td{
            padding: 0 10px;
            font-size: 12px;
            line-height: 2.5;
            color: #333;
            text-align: left;
            border: 1px solid #ccc;
            cursor: not-allowed;
            &[contenteditable]{
                cursor: default;
            }
            &.img{
                text-align: center;
                font-size: 0;
                line-height: 0;
                padding: 0;
                img{

                    max-width: 100%;
                    max-height: 100%;
                }
            }
            &:focus{
                outline: 0;
                background-color: aliceblue;
            }

        }
    }

}

